import React from "react"
import Header from "./Header";
import Footer from "./Footer";
import './timelinestyle.css'

const Timeline = () => {
  return (  
    <div>
    <Header/>
    <main class="l-main">
    <section class="about section " id="about">
    <h2 class="section-title">Experience</h2>
    <div class="timeline">

      <div class="container right">
      <div class = "tryout" style={{ backgroundImage: `url(${require('./imagestime/psl.png')})`}}></div>
      <div class="content">
      <h2>Data Scientist Internship</h2>
      <p>Publicis Sapient | May'22</p>
      <p> Automated Dynamic Pricing Decisions for commodities in E-Commerce Businesses for Revenue Maximization. Formulated complete Optimization Problem, Constraints from Scratch and gave scope for Utilization of Clustering algorithms for the same.</p>
    </div>
    </div>

    <div class="container right">
    <div class = "tryout" style={{ backgroundImage: `url(${require('./imagestime/GSoC-icon.png')})`}}></div>
      <div class="content">
      
      <a href="#gsoc"><h2>Google Summer of Code '21</h2></a>
        <p>OpenPrinting (The Linux Foundation)</p>
        <p>Primary focus of the project was to convert filters (pdftoraster, texttopdf, bannertopdf) into filter functions. These functions would be integrated in a shared library so as to reduce the number of calls to individual filter executables and minimize CPU resources exploitation.</p>
      </div>
    </div>
    </div>
    </section>
    </main>
    </div>
  )
}
export default Timeline;